import React from "react";
import { Button } from "semantic-ui-react";

function ButtonSupport(props) {

    return (
        <>
            <p style={{ margin: "auto auto auto 0" }}>{props.data.description[props.lang]}</p>
            <div className="right">
                <Button primary content={props.data.content[props.lang]} size="large" />
            </div>
        </>
    );
}

export default ButtonSupport;