import React from 'react';

import { ChromePicker } from 'react-color';
import $ from "jquery";

class ColorPicker extends React.Component {
    state = {
        background: this.props.color,
    };

    rgba2hex(color) {
        var a,
            rgb = color.replace(/\s/g, '').match(/^rgba?\((\d+),(\d+),(\d+),?([^,\s)]+)?/i),
            alpha = ((rgb && rgb[4]) || "").trim(),
            hex = rgb ?
                (rgb[1] | 1 << 8).toString(16).slice(1) +
                (rgb[2] | 1 << 8).toString(16).slice(1) +
                (rgb[3] | 1 << 8).toString(16).slice(1) : color;
        if (alpha !== "") {
            a = alpha;
        } else {
            a = 1;
        }

        a = Math.round(a * 100) / 100;
        alpha = Math.round(a * 255);
        var hexAlpha = (alpha + 0x10000).toString(16).substr(-2).toUpperCase();
        hex = hex + hexAlpha.toLowerCase();

        return hex;
    }

    handleChangeComplete = (color) => {
        this.setState({ background: "#" + this.rgba2hex(`rgba(${color.rgb.r}, ${color.rgb.g}, ${color.rgb.b}, ${color.rgb.a})`) });

        if (this.props.onChange) {
            this.props.onChange(this.props.id, this.state.background);
        }
    };

    onClickColor(e) {
        if ($(e.target).next().hasClass("dis-none")) {
            $(".option-card-settings .chrome-picker").addClass("dis-none");
            $(e.target).next().removeClass("dis-none");
            $(".option-card-settings .bg-click").css({ display: "block" });
        } else {
            $(".option-card-settings .chrome-picker").addClass("dis-none");
            $(".option-card-settings .bg-click").css({ display: "none" });
        }
    }

    hideColorPicker() {
        $(".option-card-settings .chrome-picker").addClass("dis-none");
        $(".option-card-settings .bg-click").css({ display: "none" });
    }

    render() {
        return (
            <>
                <div onClick={this.hideColorPicker} className="bg-click"></div>
                <p style={{ margin: "0" }}>{this.props.desc}</p>
                <p onClick={this.onClickColor} className="color-text" style={{ backgroundColor: this.state.background }}>hex: {this.state.background}</p>
                <ChromePicker
                    id={this.props.id}
                    className="dis-none"
                    disableAlpha={false}
                    color={this.state.background}
                    onChange={this.handleChangeComplete}
                />
            </>
        );
    }
}

export default ColorPicker;