export const getComponentValue = (id, settings = null) => {
    if (!settings) {
        if (!localStorage.getItem("settings")) {
            localStorage.setItem("settings", JSON.stringify(defaultSettings));
        }
        settings = JSON.parse(localStorage.getItem("settings"));
    }
    for (const item of settings) {
        if (item.id === id) {
            return item.value;
        }
    }
    return null;
}

export const setComponentValue = (id, value, type) => {
    if (!localStorage.getItem("settings")) {
        localStorage.setItem("settings", JSON.stringify(defaultSettings));
    }
    let settings = JSON.parse(localStorage.getItem("settings"));
    for (const item of settings) {
        if (item.id === id) {
            if (type === "switch") {
                item.value = !item.value;
            } else {
                item.value = value;
            }
            break;
        }
    }
    return settings;
}

export const setConfigValue = (id, value, child, type) => {
    if (!localStorage.getItem("settings")) {
        localStorage.setItem("settings", JSON.stringify(defaultSettings));
    }
    let settings = JSON.parse(localStorage.getItem("settings"));
    for (const item of settings) {
        if (item.id === id) {
            for (const itemConfig of item.config) {
                if (itemConfig.id === child) {
                    if (type === "switch") {
                        itemConfig.value = !itemConfig.value;
                    } else {
                        itemConfig.value = value;
                    }
                }
            }
            break;
        }
    }
    return settings;
}

export const getConfigComponent = (id, settings = null) => {
    if (!settings) {
        if (!localStorage.getItem("settings")) {
            localStorage.setItem("settings", JSON.stringify(defaultSettings));
        }
        settings = JSON.parse(localStorage.getItem("settings"));
    }
    for (const item of settings) {
        if (item.id === id) {
            return item.config;
        }
    }
    return null;
}

