import React, { useState } from "react";
import { Button, Card, Label } from "semantic-ui-react";

function Import(props) {
    const lang = props.lang;

    return (
        <div className="col" style={{ width: "100%" }} id={props.data.id}>
            <p style={{ margin: "auto auto .6em" }}>{props.data.description[lang]}</p>
            <Card className="option-card-settings" fluid color='red' header={
                <>
                    <input type="file" style={{ display: "none" }} id="btnImport" />
                    <label htmlFor="btnImport" className="ui large fluid primary button">Import</label>
                </>
            } />
        </div>
    );
}

export default Import;