import React, { Component } from "react"
import $ from "jquery"

const checkboxes = [
    { name: "random", text: { es: "Imagen aleatoria", us: "Random image" } },
    { name: "fav", text: { es: "Solo imgenes favoritas", us: "Only favorite images" } },
    { name: "selected", text: { es: "Solo imagen seleccionada", us: "Only selected image" } },
]

class ImageOptions extends Component {
    changeCheckBox(target) {
        if (target.className === "cb-active") {
            $(".cb-active").prop("checked", true);
        } else {
            $(".cb-active").prop("checked", false);
            $(".cb-active").removeClass("cb-active");
            $(`#${target.id}`).addClass("cb-active");
            localStorage.setItem("checkbox-option", target.id);
        }
    }

    checkboxSelected(active, name) {
        if (active === name)
            return (
                <input
                    type="checkbox"
                    name={name}
                    id={name}
                    defaultChecked
                    className="cb-active"
                    onChange={(e) => {
                        this.changeCheckBox(e.target)
                    }}
                />
            )
        else
            return (
                <input
                    type="checkbox"
                    name={name}
                    id={name}
                    onChange={(e) => {
                        this.changeCheckBox(e.target)
                    }}
                />
            )
    }

    defaultCheckbox() {
        if (localStorage.getItem("checkbox-option") != null)
            return localStorage.getItem("checkbox-option")
        else return "random"
    }

    render() {
        const active = this.defaultCheckbox()
        return (
            <div className="image-options">
                <div className="options">
                    {checkboxes.map((value, key) => {
                        return (
                            <div className="option" key={key}>
                                {this.checkboxSelected(active, value.name)}
                                <label htmlFor={value.name} className="text-option">{value.text[this.props.lang]}</label>
                            </div>
                        )
                    })}
                </div>
            </div>
        )
    }
}

export default ImageOptions
