import React from "react"
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"

import Home from "./page/Home"

function App() {
    return (
        <>
            <Router>
                <Switch>
                    <Route exact path="/" component={Home} />
                    <Route path="/:somthing" exact component={Home} />
                </Switch>
            </Router>
        </>
    )
}

export default App